<template>
  <section class="wrap">
    <van-nav-bar :title="name" fixed :border="false" class="header" />
    <div class="block"></div>
    <router-view></router-view>
    <div class="block"></div>
    <van-tabbar v-model="active" route>
      <van-tabbar-item icon="home-o" replace to="/">首页</van-tabbar-item>
      <van-tabbar-item icon="cluster-o" replace to="/node">节点</van-tabbar-item>
      <van-tabbar-item icon="location-o" replace to="/map">地图</van-tabbar-item>
      <van-tabbar-item icon="setting-o" replace to="/system">系统</van-tabbar-item>
    </van-tabbar>
  </section>
</template>
<script>
import { createWebSocket } from '@/api/websocket.js'
export default {
  data() {
    return {
      active: 0
    }
  },
  computed: {
    name: function() {
      return this.$store.state.headerName;
    }
  },
  created() {
    createWebSocket(this);
  }
}

</script>
<style>
.wrap {
  width: 100%;
  height: 100%;
}

.header {
  background: #55c5ff;
}

.header .van-nav-bar__left .van-icon,
.header .van-nav-bar__left .van-nav-bar__text,
.header .van-nav-bar__title {
  color: #fff;
}

.block {
  height: 46px;
}

</style>



