import store from '@/vuex/store'

var webSocketErr = 0;
var timer;
var ws;

export function createWebSocket(context) {
  //新建一个websocket连接
  ws = new WebSocket('ws://www.lovdmx.com:10001/lovdmx_control/websocket/socketServer');

  //websocket连接
  ws.onopen = function() {
    //连接成功，将错误计数清零,清除定时器
    webSocketErr = 0;
    clearTimeout(timer);

    console.log('连接成功');

  }

  //客户端接收服务端的数据
  ws.onmessage = function(res) {
    switch (JSON.parse(res.data).opCode) {
      case 2222:
        context.$store.commit('initList', JSON.parse(res.data));
        break;
      default:
        break;
    }
  }

  //通信发生错误
  ws.onerror = function() {
    //错误计数webSocketErr+1
    //关闭webSocket连接
    //重新创建webSocket连接
    //如果连续错误5次以上，停止连接，并提示用户
    webSocketErr++;
    ws.close();
    if (webSocketErr > 5) {
      alert('与服务器连接失败！');
    } else {
      timer = setTimeout(function() {
        createWebSocket();
      }, 1000)
    }
  }

  //通信断开
  ws.onclose = function() {
    console.log('websocket断开连接了')
  }
};



