// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './vuex/store'

// 引入vant组件库
import {Field,Checkbox, CheckboxGroup,button,Cell,CellGroup,Image,Toast,Tabbar, TabbarItem,Collapse, CollapseItem,Dialog,NavBar} from 'vant';
Vue.use(Field)
   .use(Checkbox)
   .use(Cell)
   .use(CheckboxGroup)
   .use(button)
   .use(CellGroup)
   .use(Image)
   .use(Toast)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Collapse)
   .use(CollapseItem)
   .use(Dialog)
   .use(NavBar)


// 引入百度地图
import BaiduMap from 'vue-baidu-map';
Vue.use(BaiduMap,{ ak:'Kbnu8vBKPYnDk1tnMKju1y3nb6QqDeT7' });

//引入公共js
import Common from './utils/common.js'
Vue.prototype.common=Common

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



