<template>
  <section>
    <div class="header">
    	<img src="../assets/images/bg.png" >
    </div>
    <van-cell-group :border="false" class="login">
      <van-field v-model="userName" required clearable label="用户名" placeholder="请输入用户名" />
      <van-field v-model="password" type="password" label="密码" placeholder="请输入密码" required />
      <van-checkbox v-model="remember">记住密码</van-checkbox>
      <div class="btn"><van-button type="primary" @click="login">登 录</van-button></div>
    </van-cell-group>
  </section>
</template>
<script>
export default {
  data(){
  	return {
  		userName:'',
  		password:'',
  		remember:false
  	}
  },
  methods:{
  	login(){
  		this.common.login(this.userName,this.password,this.remember,'/');
  	}
  },
  created(){
  	// 在页面加载时从cookie获取登录信息
    let userName = this.common.getCookie("userName")
    let password = Base64.decode(this.common.getCookie("password"))
    let remember = this.common.getCookie("remember")
    this.userName = userName ? userName : '';
    this.password = password ? password : '';
    this.remember = remember==='true' ? true : false;
  }
}

</script>
<style>
section {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.header img{
	width:100%;
	height:auto;
	display: block;
}

.login{
	padding:20px 12px;
}

.login .van-checkbox{
	margin:12px 0;
}

.login .btn{
	text-align: center;
}

.login .van-button{
	width:80%;
	border-radius: 12px;
}

</style>



