<template>
  <section class="map">
    <vueMap></vueMap>
  </section>
</template>
<script>
import vueMap from '@/components/map'
export default {
  data() {
    return {

    }
  },
  components: {
    vueMap
  },
  created() {
    var name = this.$router.currentRoute.name;
    this.$store.commit('setHeaderName', name);
  }
}

</script>
<style>
	.map{
		width:100%;
		height:calc(100% - 100px);
	}
</style>



