import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    headerName:'',
    deviceList:[]
  },
  mutations: {
  	initList(state, payload) {
      state.deviceList = payload.deviceDataJson;
    },
    setHeaderName(state, payload){
      state.headerName=payload;
    }
  }
})



