<template>
  <section>
    <van-nav-bar :title="name" left-text="返回" :right-text="isOnline" left-arrow @click-left="$router.go(-1)" fixed :border="false" class="header" :class="{'online':isOnline==='在线','offline':isOnline==='离线'}" />
    <div class="block"></div>
    <van-collapse v-model="activeNames">
      <van-collapse-item v-for="(item,index) in relayList" :title="item.rs485toNetIp+'-'+item.slaveAddr" :name="index+1">
        <van-cell-group :border="false">
          <van-cell v-for="(item2,index2) in item.loopList " :title="index2+1" :value="item2?'打开':'关闭'" />
        </van-cell-group>
      </van-collapse-item>
    </van-collapse>
  </section>
</template>
<script>
import { getNodeList } from '@/api/api.js'
export default {
  data() {
    return {
      name: '',
      isOnline: '',
      relayList: [],
      activeNames: []
    }
  },
  methods: {
    //获取继电器列表
    getRelayList(mac) {
      const that = this;
      getNodeList({ gatewayMac: mac }).then(res => {
        if (res.result === 'true') {
          for (var i = 0; i < res.relayList.length; i++) {
            var loopList = getLoopList(res.relayList[i].electricRelayLoopON, res.relayList[i].electricRelayLoopOFF);
            res.relayList[i].loopList = loopList;
          }
          that.relayList = res.relayList;
        }
      });
    }
  },
  created() {
    var query = this.$router.currentRoute.query;
    this.name = query.rackName;
    this.getRelayList(query.mac);
    this.isOnline = query.isOnline===1?'在线':'离线';
  }
}

//生成回路列表
function getLoopList(onList, offList) {
  var trueList = onList.split(',');
  var falseList = offList.split(',');
  var arr = [];
  for (var i = 0; i < trueList.length; i++) {
    if (trueList[i]) {
      arr[parseInt(trueList[i])] = true;
    }
  }
  for (var i = 0; i < falseList.length; i++) {
    if (falseList[i]) {
      arr[parseInt(falseList[i])] = false;
    }
  }
  return arr;
}

</script>
<style>
  .online .van-nav-bar__right .van-nav-bar__text{
    color:#ffffff;
  }

  .offline .van-nav-bar__right .van-nav-bar__text{
    color:#ff4444;
  }
</style>



