import router from '@/router/index.js';
import { login, logout } from '@/api/api.js';
import {Toast} from 'vant';
import Vue from 'vue';
Vue.use(Toast);
// 引入base64
const Base64 = require('js-base64').Base64;

// 获取cookie
const getCookie = function(key) {
  var name = key + "=";
  var ca = document.cookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i].trim();
    if (c.indexOf(name) == 0) return c.substring(name.length, c.length);
  }
  return "";
}

// 储存cookie
const setCookie = function(key, value, extime) {
  var d = new Date();
  d.setTime(d.getTime() + (extime* 1000));
  var expires = "expires=" + d.toGMTString();
  document.cookie = key + "=" + value + "; " + expires;
}

export default {
  /**
   * 登录
   * @param  {String} userName 账号
   * @param  {String} password 密码
   * @param  {Boolean} remember 是否记住密码
   * @param  {String} path     登录成功后跳转的路劲
   */
  login: function(userName, password, remember, path) {
    var params = {
      userName: userName,
      password: password,
      remember: remember
    }
    login(params).then(res => {
      console.log(res)
      /**
       * 0--超级用户
       * 1--普通用户
       * 2--账号不存在
       * 3--密码错误
       * 4--账号已禁用
       * 5--错误
       */
      switch (res.flag) {
        case '0':
          sessionStorage.setItem('token',JSON.stringify(res.account))
          router.push({ path: path });
          setUserInfo(userName, password, remember);
          break;
        case '1':
          break;
        case '2':
          Toast.fail('账号不存在');
          break;
        case '3':
          Toast.fail('密码错误');
          break;
        case '4':
          Toast.fail('账号已被禁用');
          break;
        case '5':
          Toast.fail('账号已在线');
          break;
        default:
          Toast.fail('未知错误');
          break;
      }
    })
  },
  
  /**
   * 退出登录
   * 获取cookie中的'remember',如果remember为'false'，则表示不用记住密码，将cookie中的userName,password都清空
   */
  logout: function() {
    logout().then(res => {
      if (res) {
        var remember=getCookie('remember');
        if(remember==='false'){
          setCookie('userName','',null);
          setCookie('password','',null);
        }
        router.push('/login')
      }
    })
  },

  getCookie,

  setCookie
}

//保存登录信息
function setUserInfo(userName, password, remember) {
  // 将登录信息保存到cookie，如果没有勾选记住密码，在用户退出登录时会清除cookie
  setCookie("userName", userName, 31536000);
  // base64加密密码
  var password = Base64.encode(password)
  setCookie("password", password, 31536000);
  setCookie("remember", remember, 31536000);
}



