<template>
  <van-cell-group>
    <van-cell v-for="item in gatewayList" :title="item.rackName" :value="item.mode" @click="$router.push({path:'/relay',query:{mac:item.mac,rackId:item.rackId,isOnline:item.isOnline,rackName:item.rackName}})" />
  </van-cell-group>
</template>
<script>
import { getGatewayList } from '@/api/api.js'
export default {
  data() {
    return {
      gatewayList: []
    }
  },
  methods: {
    //获取智能网关列表
    getGatewayList() {
      const that = this;
      getGatewayList().then(res => {
        if (res.result === 'true') {
          console.log(res)
          var gatewayList = [];
          for (var i = 0; i < res.intelligentGatewayList.length; i++) {
            gatewayList[i] = res.intelligentGatewayList[i];
            gatewayList[i].mode = gatewayList[i].isOnline === 1 ? '在线' : '离线';
          }
          that.gatewayList = gatewayList;
        }
      })
    }
  },
  created() {
    var name = this.$router.currentRoute.name;
    this.$store.commit('setHeaderName', name);
    this.getGatewayList()
  }
}

</script>
<style></style>



