<template>
  <section>
    <van-collapse v-model="activeNames">
      <van-collapse-item title="用户信息" name="1">内容</van-collapse-item>
      <van-collapse-item title="当前版本" name="2">内容</van-collapse-item>
      <van-collapse-item title="用户指南" name="3">内容</van-collapse-item>
      <van-collapse-item title="关于" name="4">内容</van-collapse-item>
    </van-collapse>
    <div class="btn">
      <van-button type="danger" @click="logout">退出登录</van-button>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      activeNames: []
    }
  },
  methods: {
    logout() {
      const that = this;
      this.$dialog.confirm({
        title: '提示',
        message: '确认退出当前账号'
      }).then(() => {
        that.common.logout();
      }).catch(()=>{
      	return;
      })
    }
  },
  created() {
    var name = this.$router.currentRoute.name;
    this.$store.commit('setHeaderName', name);
  }
}

</script>
<style>
.btn {
  text-align: center;
  margin-top: 20px;
}

.btn .van-button {
  width: 80%;
  border-radius: 12px;
}

</style>



