import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login'
import home from '@/pages/home'
import index from '@/pages/index'
import node from '@/pages/node'
import relay from '@/pages/relay'
import pageMap from '@/pages/map'
import system from '@/pages/system'

Vue.use(Router)

export const router= new Router({
  routes: [{
    path: '/login',
    name: '登录',
    component: login
  }, {
    path: '/',
    component: home,
    children: [{ path: '/', name: '首页', component: index, meta: { requireAuth: true } }]
  }, {
    path: '/',
    component: home,
    children: [{ path: '/node', name: '节点', component: node, meta: { requireAuth: true } }]
  },{
    path: '/relay',
    name:'继电器',
    component: relay,
    meta: { requireAuth: true }
  }, {
    path: '/',
    component: home,
    children: [{ path: '/map', name: '地图', component: pageMap, meta: { requireAuth: true } }]
  }, {
    path: '/',
    component: home,
    children: [{ path: '/system', name: '系统', component: system, meta: { requireAuth: true } }]
  }]
})

export default router;

//路由守卫，判断路由是否需要权限校验
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) { // 如果路由项需要权限校验
    /* 
      从localStorage拿出token码，并判断token码是否过期
    */
    var token = sessionStorage.getItem("token")
    if (token) {
      next() // 正常跳转页面
    } else {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
        /* 将跳转的路由地址作为参数带给登陆页，登录成功后跳转回该页面 */
      })
    }
  } else { // 如果不需要权限校验，直接进入路由页面
    next();
  }
})



