<template>
  <baidu-map class="baidu-map" :center="center" :zoom="zoom" map-type="BMAP_NORMAL_MAP" @ready="handler" scroll-wheel-zoom :map-click="false">
    <!-- 地图类型控件 -->
    <bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']" anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-map-type>
    <!-- 标记点 -->
    <bm-marker v-for="item in markers" :position="{lng:item.rackLongitude,lat:item.rackLatitude}"></bm-marker>
    <!-- 城市列表 -->
    <bm-city-list anchor="BMAP_ANCHOR_TOP_LEFT"></bm-city-list>
  </baidu-map>
</template>
<script>
export default {
  data() {
    return {
      center: { lng: 0, lat: 0 },
      zoom: 3
    }
  },
  computed:{
    markers:function(){
      return this.$store.state.deviceList.sonRackDeviceList;
    }
  },
  methods: {
    handler({ BMap, map }) {
      this.center.lng = 103.8127504728
      this.center.lat = 36.0650310424
      this.zoom = 18
    }
  },
  created(){
    
  }
}

</script>
<style>
.baidu-map {
  width: 100%;
  height:100%;
  position: relative;
}

/*隐藏百度地图logo*/
.anchorBL {
  display: none;
}

</style>



